.product
{
    width: 25%;
    height: fit-content;
}
.shop
{
    width: 100%;
    height: 900px;
    display: flex;
    gap: 50px;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
}
.photo
{
    padding-top: 30px;
    padding-bottom: 40px;
}
.dis
{
    border-top: 3px solid gray;
}
.buy
{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.buy button
{
    width: fit-content;
    text-decoration: none;
    background-color: black;
    color: #FFF;
    border: 1px solid black;
    padding: 7px;
}
.buy button:hover
{
    background-color: gray;
    border-color: gray;
    color: black;
}
