.added
{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding-top: 30px;
    padding-bottom: 60px;
}
.added h1
{
    padding: 20px;
    text-align: center;
}
.divition
{
    width: 90%;
    display: flex;
    justify-content: space-around;
    padding: 30px;
    text-align: end;
}
.pro
{
    width: 25%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
}
.nme
{
    width: 25%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
}
.qty
{
    width: 25%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cast
{
    width: 25%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
}
.whole
{
    width: 90%;
    height: fit-content;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 30px;
    border: 1.5px solid gray;
}
.whole button
{
    width: 23px;
    height: 23px;
}
.picture
{
    width: 25%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
}
.name
{
    width: 25%;
    height: fit-content;
    justify-content: center;
    align-items: center;
}
.incre
{
    width: 25%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.amt
{
    width: 25%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
}
.addprice
{
    width: 80%;
    height: 130px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.coupon
{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
}
.coupon input
{
    width: 200px;
    height: 35px;
    font-size: 25px;
    border: 1.5px solid gray;
    outline: none;
    color: #8F8C87;
    padding: 10px;
}
.coupon button
{
    width: 70px;
    height: 54px;
    border: 1.5px solid gray;
    outline: none;
    background-color: black;
    color: #FFF;
}
.check
{
    width: 80%;
    height: 200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.check a
{
    width: 200px;
    height: 45px;
    font-size: 17px;
    color: white;
    background-color: black;
    text-decoration: none;
    border: 1.5px solid gray;
    display: flex;
    justify-content: center;
    align-items: center;

}