.links
{
    width: 100%;
    height: 90px;
    display: flex;
    background-color: black;
    color: white;
    justify-content: space-between;
    align-items: center;
}
.links h1
{
    font-family: 'Orbitron', sans-serif;
    padding-left: 30px;
}
.links a 
{
    text-decoration: none;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 1.0rem;
    color: white;
}
.con
{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    display: flex;
    gap: 90px;
    color: white;
    padding-right: 40px;
}
.search
{
    width: 200px;
    height: 25px;
    background-color: white;
    display: flex;
    justify-content: center;
    border-radius: 25px;
}
#search 
{
    border: none;
    background-color: white;
    outline: none;
    color: black;
}
.icons
{
    display: flex;
    gap: 40px;
    align-items: center;
}